import React from "react";

type HeaderMessagesType = {
    onChatClose:() => void
    nickName:string
}
export const HeaderMessages:React.FC<HeaderMessagesType> = React.memo(props => {
    const {onChatClose, nickName} = props;

    return (
        <div className={`online-users-header nickName`}>
            <div onClick={onChatClose} style={{cursor: 'pointer'}}>
                <svg xmlns="http://www.w3.org/2000/svg" width="25"
                     height="25" fill="currentColor"
                     className="bi bi-arrow-left" viewBox="0 0 16 16">
                    <path
                        d="M15 8a.5.5 0 0 0-.5-.5H2.707l3.147-3.146a.5.5 0 1 0-.708-.708l-4 4a.5.5 0 0 0 0 .708l4 4a.5.5 0 0 0 .708-.708L2.707 8.5H14.5A.5.5 0 0 0 15 8z"/>
                </svg>
            </div>
            <div>
                {nickName}
            </div>
        </div>
    )
})